.site-caption {
    padding-left: 30px;
    padding-right: 30px;
    font-weight: 600;
    text-align: center;
    text-transform: uppercase;
}

.site-caption__title {
    padding-top: 60px;
    padding-bottom: 8px;
    font-size: 30px;
    font-weight: 700;
    color: $black-color;
    @media (max-width: 768px) {
        padding-top: 40px;
    }
}

.site-caption__description {
    color: $light-color;
}