.slick-dots li.slick-active > button {
  background-color: #B88B58;
}
.slick-dots button {
  padding: 5px;
  font-size: 0;
  background-color: transparent;
  border: 2px solid #B88B58;
  border-radius: 50%;
  cursor: pointer;
}
@media (orientation: portrait) {
  .slick-dots {
    bottom: 15%;
  }
}

.site-header__slide {
  position: relative;
  height: 100%;
  background-size: cover;
  background-position: center 5px;
}

.site-header__content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -35%);
  text-transform: uppercase;
  text-align: center;
  color: #FFFFFF;
}

.site-header__title {
  margin-bottom: 26px;
  font-size: 72px;
  line-height: 0.9em;
}
.site-header__title span {
  color: #B88B58;
}
@media (max-width: 992px) {
  .site-header__title {
    font-size: 60px;
  }
}
@media (max-width: 768px) {
  .site-header__title {
    font-size: 48px;
  }
}
@media (max-width: 572px) {
  .site-header__title {
    font-size: 42px;
  }
}
@media (max-width: 480px) {
  .site-header__title {
    font-size: 34px;
  }
}

.site-header__description {
  margin-bottom: 50px;
  font-size: 24px;
  font-weight: 600;
}
.site-header__description a {
  color: inherit;
  transition: all 0.2s;
}
.site-header__description a:hover, .site-header__description a:focus {
  color: #B88B58;
}
@media (max-width: 480px) {
  .site-header__description {
    font-size: 22px;
  }
}

.site-main {
  position: relative;
}

.site-main__btn-up {
  position: absolute;
  left: -60px;
  width: 64px;
  height: 64px;
}
@media (max-width: 1320px) {
  .site-main__btn-up {
    left: 20px;
  }
}

.site-section:nth-child(odd) {
  background-color: #EDEDED;
}
.site-section:nth-child(even) {
  background-color: #F7F7F7;
}