.site-header {
    position: relative;
    height: 100vh;
    min-height: 600px;
}

.site-header__topline {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 10;
}

.site-header__slider {
    height: 100%;
    .slick-list {
        height: 100%;
    }
    .slick-track {
        height: 100%;
    }
}

.slick-dots {
    position: absolute;
    bottom: 8%;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    justify-content: space-between;
    max-width: 64px;
    width: 100%;
    li.slick-active > button {
        background-color: $accent-color;
    }
    button {
        padding: 5px;
        font-size: 0;
        background-color: transparent;
        border: 2px solid $accent-color;
        border-radius: 50%;
        cursor: pointer;
    }
    @media (orientation: portrait) {
        bottom: 15%;
    }
}

.site-header__slide {
    position: relative;
    height: 100%;
    background-size: cover;
    background-position: center 5px;
}

.site-header__content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -35%);
    text-transform: uppercase;
    text-align: center;
    color: $inverse-color;
}

.site-header__title {
    margin-bottom: 26px;
    font-size: 72px;
    line-height: .9em;
    span {
        color: $accent-color;
    }

    @media (max-width: 992px) {
        font-size: 60px;
    }

    @media (max-width: 768px) {
        font-size: 48px;
    }

    @media (max-width: 572px) {
        font-size: 42px;
    }

    @media (max-width: 480px) {
        font-size: 34px;
    }
}

.site-header__description {
    margin-bottom: 50px;
    font-size: 24px;
    font-weight: 600;
    a {
        color: inherit;
        transition: all .2s;
        &:hover, &:focus {
            color: $accent-color;
        }
    }
    @media (max-width: 480px) {
        font-size: 22px;
    }
}