.section-price__content {
    padding-top: 46px;
    padding-bottom: 62px;
    @media (max-width: 768px) {
        padding-top: 40px;
        padding-bottom: 60px;
    }
}

.section-price__list {
    display: flex;
    margin-left: -15px;
    margin-right: -15px;
    @media (max-width: 992px) {
        flex-wrap: wrap;
        justify-content: center;
        margin-bottom: -30px;
    }
}

.section-price__item {
    flex-grow: 1;
    flex-shrink: 1;
    flex-basis: calc(25% - 30px);
    margin-right: 15px;
    margin-left: 15px;
    @media (max-width: 992px) {
        flex-basis: calc(50% - 30px);
        max-width: 360px;
        margin-bottom: 30px;
    }
    @media (max-width: 576px) {
        flex-basis: 100%;
    }
}