.site-footer {
    background-color: $black-color;
}

.site-footer__content {
    display: flex;
    justify-content: space-between;
    max-width: 760px;
    padding-top: 36px;
    padding-bottom: 48px;
    @media (max-width: 768px) {
        flex-wrap: wrap;
        justify-content: center;
    }
}

.site-footer__info {
    flex-basis: 274px;
    display: flex;
    flex-direction: column;
    @media (max-width: 768px) {
        order: 1;
        flex-basis: 286px;
    }
}

.site-footer__social {
    display: flex;
    justify-content: space-between;
    @media (max-width: 768px) {
        margin-bottom: 20px;
    }
}

.site-footer__social-item {
    flex-shrink: 0;
    width: 34px;
    height: 34px;
}

.site-footer__social-link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    background-color: $bg-dark;
    border-radius: 50%;
    transition: background-color .2s;
    &:hover, &:focus {
        background-color: $accent-color;
    }
}

.site-footer__description {
    max-width: 370px;
    font-size: 12px;
    font-weight: 500;
    line-height: 21px;
    color: $text-gray;
    @media (max-width: 768px) {
        flex-basis: 100%;
        max-width: 100%;
        text-align: center;
        margin-bottom: 20px;
    }
    
}

.site-footer__copyright {
    margin-top: auto;
    font-weight: 500;
    text-transform: uppercase;
    color: $inverse-color;
    @media (max-width: 768px) {
        align-self: center;
    }
}