.header-menu {
	background-color: $black-color;
}

.header-menu__content {
	display: flex;
	justify-content: space-between;
}

.header-menu__logo {
	padding-top: 22px;
	padding-bottom: 22px;
	margin-right: 40px;
	&:hover,
	&:focus {
		.header-menu__logo svg > * {
			fill: #fff;
		}
	}
	@media (max-width: 992px) {
        padding-top: 16px;
		padding-bottom: 16px;
    }
}

.header-menu__list {
	display: flex;
	justify-content: space-between;
	max-width: 900px;
	width: 100%;
    @media (max-width: 992px) {
        display: none;
    }
}

.header-menu__item {
	position: relative;
	display: flex;
	align-items: center;
	&.is-active::after {
		content: "";
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		height: 5px;
		background-color: $accent-color;
	}
}

.header-menu__link {
	padding-top: 20px;
	padding-bottom: 20px;
	font-size: 14px;
	font-weight: 700;
	text-transform: uppercase;
	color: $inverse-color;
	transition: color 0.2s;
	&:hover,
	&:focus {
		color: $accent-color;
	}
}

.header-menu__hamburger {
    display: none;
	align-self: center;
	padding-right: 0;
	&:hover,
	&:focus {
		.hamburger-inner,
		.hamburger-inner::before,
		.hamburger-inner::after {
			background-color: $accent-color;
		}
	}
    @media (max-width: 992px) {
        display: block;
    }
}
