.section-about__content {
    display: flex;
    padding-top: 54px;
    padding-bottom: 80px;
    @media (max-width: 992px) {
        flex-wrap: wrap;
        justify-content: center;
    }
    @media (max-width: 768px) {
        padding-top: 40px;
        padding-bottom: 60px;
    }
}

.section-about__info {
    flex-grow: 1;
    flex-shrink: 1;
    @media (max-width: 992px) {
        order: 1;
        text-align: center;
    }
}

.section-about__video {
    flex-shrink: 0;
    max-width: 445px;
    margin-left: 30px;
    @media (max-width: 992px) {
        flex-shrink: 1;
        max-width: 700px;
        margin-left: 0;
        margin-bottom: 30px;
    }
    @media (max-width: 576px) {
        img {
            width: 100%;
        }
    }
}

.section-about__description {
    margin-bottom: 34px;
    font-weight: 500;
    line-height: 25px;
    @media (max-width: 992px) {
        margin-bottom: 30px;
    }
}