// USER VARIABLES SECTION

$accent-color: #B88B58;
$inverse-color: #FFFFFF;
$black-color: #000000;
$light-color: #aeaeae;

$bg-dark: #373737;
$bg-light: #F7F7F7;
$bg-gray: #EDEDED;
$font-size: 14px; 

$text-default: #242424;
$text-light: #9e9e9e;
$text-gray:#7e7e7e;

$user-font: montserrat-oh5, sans-serif;

// IMPORT SECTION

@import 'node_modules/hamburgers/_sass/hamburgers/hamburgers.scss';
@import 'node_modules/slick-carousel/slick/slick.scss';

// FONTS LOAD SECTION

@mixin font($fname, $fstyle, $fweight, $furl, $fformat) {
	@font-face {
		font-family: $fname;
		font-style: $fstyle;
		font-weight: $fweight;
    font-display: swap;
		src: url($furl) format($fformat);
	}
}

@include font('montserrat-oh5', normal, 300, '../fonts/montserrat-light-webfont.woff2', 'woff2');
@include font('montserrat-oh5', normal, 400, '../fonts/montserrat-regular-webfont.woff2', 'woff2');
@include font('montserrat-oh5', normal, 500, '../fonts/montserrat-medium-webfont.woff2', 'woff2');
@include font('montserrat-oh5', normal, 600, '../fonts/montserrat-semibold-webfont.woff2', 'woff2');
@include font('montserrat-oh5', normal, 700, '../fonts/montserrat-bold-webfont.woff2', 'woff2');
@include font('montserrat-oh5', normal, 300, '../fonts/montserrat-light-webfont.woff', 'woff');
@include font('montserrat-oh5', normal, 400, '../fonts/montserrat-regular-webfont.woff', 'woff');
@include font('montserrat-oh5', normal, 500, '../fonts/montserrat-medium-webfont.woff', 'woff');
@include font('montserrat-oh5', normal, 600, '../fonts/montserrat-semibold-webfont.woff', 'woff');
@include font('montserrat-oh5', normal, 700, '../fonts/montserrat-bold-webfont.woff', 'woff');

// GENERAL DOCUMENT STYLES

::placeholder {
	color: #666;
}

::selection {
	background-color: $accent-color;
	color: #fff;
}

input, textarea {
	outline: none;
  padding: 0;
  margin: 0;
}

*,
*:after,
*:before {
  box-sizing: inherit;
}

html {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}

body,
html {
  box-sizing: border-box;
}

body {
  position: relative;
  min-width: 320px;
  padding: 0;
  margin: 0;
	font-family: $user-font;
	font-size: $font-size;
	color: $text-default;
	overflow-x: hidden;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

ul, dl {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

p {
  margin: 0;
  padding: 0;
}

img {
  border-style: none;
  display: block;
  max-width: 100%;
}

a {
  text-decoration: none;
}

a, button {
  outline: none;
}

.container {
  position: relative;
  max-width: 1170px;
  padding-left: 30px;
  padding-right: 30px;
  margin-left: auto;
  margin-right: auto;
  @media (max-width: 576px) {
    padding-left: 20px;
    padding-right: 20px;
}
}

.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  min-width: 194px;
  height: 45px;
  padding: 16px 20px;
  font-family: $user-font;
  font-size: 12px;
  font-weight: 700;
  text-transform: uppercase;
  background-color: $accent-color;
  color: $inverse-color;
  border-radius: 30px;
  border: none;
  transition: background-color .2s;
  user-select: none;
  cursor: pointer;
  &:hover, &:focus {
    background-color: $black-color;
  }
  &_dark {
    background-color: $bg-dark;
    &:hover, &:focus {
      background-color: $accent-color;
    }
  }
  &_is-call {
    position: absolute;
    bottom: -32px;
    right: -60px;
    min-width: 0px;
    width: 64px;
    height: 64px;
    padding: 0;
    border-radius: 50%;
    svg {
      width: 40px;
      height: 40px;
    }
    @media (max-width: 1320px) {
      right: 20px;
    }
  }
  &_is-up {
    position: fixed;
    bottom: 60px;
    min-width: 0;
    width: 64px;
    height: 64px;
    border-radius: 50%;
    transition: background-color .2s, opacity .2s, visibility .2s;
    opacity: 0;
    visibility: hidden;
    z-index: 1000;
    cursor: pointer;
    &.show {
      opacity: 1;
      visibility: visible;
    }
    &::after {
        content: '';
        display: block;
        width: 24px;
        height: 24px;
        margin-top: 8px;
        border-right: 2px solid $inverse-color;
        border-bottom: 2px solid $inverse-color;
        transform: rotate(-135deg);
    }
    &:hover, &:focus {
        background-color: $bg-dark;
    }
  }
}