.section-contacts__content {
    max-width: 540px;
    padding-top: 92px;
    padding-bottom: 56px;
    margin-left: auto;
    margin-right: auto;
    @media (max-width: 768px) {
        padding-top: 60px;
    }
}

.section-contacts__form {
    display: flex;
    flex-wrap: wrap;
}

.section-contacts__input,
.section-contacts__textarea {
    position: relative;
    flex: 1;
    flex-basis: 100%;
    padding-bottom: 12px;
    margin-bottom: 46px;
    font-family: $user-font;
    font-size: 13px;
    font-weight: 500;
    text-transform: uppercase;
    background-color: transparent;
    border: none;
    border-bottom: 2px solid $light-color;
    transition: border 0.2s;
    &::placeholder {
        color: $text-light;
    }
    &:focus {
        border-color: currentColor;
        &::placeholder {
            position: absolute;
            width: 1px;
            height: 1px;
            overflow: hidden;
            clip: rect(0 0 0 0);
        }
    }
}

.section-contacts__textarea {
    overflow: auto;
    resize: none;
    &::placeholder {
        padding-top: 16px;
    }
}

.section-contacts__input:first-child,
.section-contacts__input:first-child + .section-contacts__input {
    flex-basis: calc(50% - 30px);
    @media (max-width: 576px) {
        flex-basis: 100%;
    }
}

.section-contacts__input:first-child {
    margin-right: 30px;
    @media (max-width: 576px) {
        margin-right: 0px;
    }
}

.section-contacts__send {
    margin-left: auto;
    margin-right: auto;
}
