.price-card {
    max-width: 500px;
    padding: 34px;
    text-align: center;
    background-color: $inverse-color;
}

.price-card__type-plan {
    display: inline-block;
    margin-bottom: 12px;
    font-weight: 600;
    text-transform: uppercase;
}

.price-card__value {
    font-size: 30px;
    span {
        letter-spacing: 0.16em;
    }
}

.price-card__period {
    display: inline-block;
    margin-bottom: 24px;
    font-size: 10px;
    font-weight: 600;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: $light-color;
}

.price-card__permissions {
    margin-bottom: 28px;
}

.price-card__permissions-item {
    font-size: 12px;
    font-weight: 600;
    color: $text-gray; 
    &:not(:last-child) {
        &::after {
            content: '';
            display: block;
            width: 10px;
            height: 1px;
            margin: 8px auto;
            background-color: currentColor;
        }
    }
}

.price-card__button {
    min-width: 130px;
}