.adaptive-menu {
  display: none;
}

.adaptive-menu__title {
  padding: 20px;
  text-align: center;
  border-bottom: 1px solid $bg-dark;
}

.adaptive-menu__link {
  padding: 20px;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  color: $inverse-color;
  border-bottom: 1px solid $bg-dark;
  transition: color 0.2s;
  &:hover,
  &:focus {
    color: $accent-color;
  }
}

.slideout-menu {
  position: fixed;
  top: 0;
  bottom: 0;
  width: 256px;
  min-height: 100vh;
  background-color: $black-color;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  z-index: 0;
  display: none;
}

.slideout-menu-left {
  left: 0;
}

.slideout-menu-right {
  right: 0;
}

.slideout-panel {
  position: relative;
  z-index: 1;
  will-change: transform;
  min-height: 100vh;
}

.slideout-open,
.slideout-open body,
.slideout-open .slideout-panel {
  overflow: hidden;
}

.slideout-open .slideout-menu {
  display: flex;
  flex-direction: column;
}
